import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { isIE } from 'react-device-detect'
import {
  Menu,
  Image,
  Card,
  Breadcrumb,
  Segment,
  Label,
  Icon,
} from 'semantic-ui-react'
import { getWeather } from '../reducers/weatherReducer'
import { getTimeZone } from '../reducers/timeZoneReducer'
import {
  initializeCountries,
  filterCountriesByRegion,
  filterCountriesBySubRegion,
} from '../reducers/countryReducer'
import { resetCountryNews } from '../reducers/countryDataReducer'

import zoomLevel from '../zoomLevels'
import loadable from '@loadable/component'
import WeatherIE from '../components/WeatherIE'

const News = loadable(() => import('../components/News'))
const Map = loadable(() => import('../pages/Map'))
const Weather = loadable(() => import('../components/Weather'))

const Country1 = ({ data }) => {
  const [loading, setLoading] = useState(true)
  const [activeTab, setActiveTab] = useState('Map')

  const dispatch = useDispatch()

  const status = useSelector((state) => state.country.news.status)
  const articles = useSelector((state) => state.country.news.articles)


  useEffect(() => {
    if (articles && articles.length > 0) {
      setActiveTab('News')
      setLoading(false)
    } else {
      setLoading(false)
    }
  }, [status])

  const unit = useSelector((state) => state.unit.unit)

  const location =
    data.capitalInfo.latlng === undefined
      ? data.latlng
      : data.capitalInfo.latlng

  useEffect(() => {
    dispatch(
      getWeather({
        location: location,
        unit: unit,
      })
    )
    dispatch(getTimeZone({ location: location }))
  }, [unit])

  const handleTabClick = (e, { name }) => {
    setActiveTab(name)
  }

  const handleBreadcrumb = (val) => {
    dispatch(resetCountryNews())
    if (val === 'All') {
      dispatch(initializeCountries())
    } else if (val === 'region') {
      dispatch(filterCountriesByRegion(data.region))
    } else if (val === 'subregion') {
      dispatch(filterCountriesBySubRegion(data.subregion))
    }
    setLoading(true)
  }

  return loading ? (
    <>
      <Icon loading name="circle notch" />
    </>
  ) : (
    <>
      <Segment attached="bottom">
        <Menu id="breadcrumb" secondary>
          <Breadcrumb size="small">
            <Breadcrumb.Section
              key="All"
              link
              onClick={() => handleBreadcrumb('All')}
            >
              All
            </Breadcrumb.Section>
            <Breadcrumb.Divider icon="right chevron" />
            <Breadcrumb.Section
              key={data.region}
              link
              onClick={() => handleBreadcrumb('region')}
            >
              {data.region}
            </Breadcrumb.Section>
            {data.subregion !== '' ? (
              <>
                <Breadcrumb.Divider icon="right chevron" />
                <Breadcrumb.Section
                  key={data.subregion}
                  link
                  onClick={() => handleBreadcrumb('subregion')}
                >
                  {data.subregion}
                </Breadcrumb.Section>
                <Breadcrumb.Divider icon="right chevron" />
                <Breadcrumb.Section key={data.name.common} active>
                  {data.name.common}
                </Breadcrumb.Section>
              </>
            ) : (
              <></>
            )}
          </Breadcrumb>
        </Menu>
        <Menu attached tabular className="ui menu2">
          {articles.length > 0 ? (
            <Menu.Item
              name="News"
              active={activeTab === 'News'}
              onClick={handleTabClick}
            />
          ) : (
            <></>
          )}
          <Menu.Item
            name="Map"
            active={activeTab === 'Map'}
            onClick={handleTabClick}
          />
          <Menu.Item
            name="Flag"
            active={activeTab === 'Flag'}
            onClick={handleTabClick}
          />{' '}
          <Menu.Item
            name="CoatOfArms"
            active={activeTab === 'CoatOfArms'}
            onClick={handleTabClick}
          />
          <Menu.Item
            name="Weather"
            active={activeTab === 'Weather'}
            onClick={handleTabClick}
          />
        </Menu>
        {articles && articles.length > 0 && activeTab === 'News' ? (
          <>
            <News />
          </>
        ) : (
          <></>
        )}
        {activeTab === 'Map' ? (
          <>
            <Map lt={data.latlng[0]} lg={data.latlng[1]} zm={zoomLevel(data)} />
          </>
        ) : (
          <></>
        )}
        {activeTab === 'Flag' ? (
          <Image
            id="flag"
            centered
            src={isIE ? data.flags.png : data.flags.svg}
            alt="country flag"
            size="medium"
            bordered
          />
        ) : (
          <></>
        )}
        {activeTab === 'CoatOfArms' ? (
          <>
            {Object.entries(data.coatOfArms).length === 0 ? (
              <Label>No image available</Label>
            ) : (
              <Image
                id="coatOfArms"
                centered
                src={isIE ? data.coatOfArms.png : data.coatOfArms.svg}
                alt="coat of arms"
                size="medium"
                bordered
              />
            )}
          </>
        ) : (
          <></>
        )}
        {activeTab === 'Weather' ? (
          <>
            <Card id="CiaCard" fluid>
              {isIE ? <WeatherIE country={data} /> : <Weather country={data} />}
            </Card>
          </>
        ) : (
          <></>
        )}
      </Segment>
    </>
  )
}

const Country = React.memo(Country1)

export default Country
